*,
*::after,
*::before {
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
}

body {
  font-family:  'Gotham Pro';
  font-weight: normal;
  animation: fadein 1s;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}