.cookie-policy {
  position: fixed;
  top: 0;
  background-color: #fff;
  max-width: 470px;
  right: 0;
  z-index: 10;
  padding: 30px 50px 40px;
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.05);

  @media screen and (min-width: $medium-screen) {
    width: 50vw;
    top: auto;
    bottom: 20px;
  }
}

.cookie-policy__text {
  color: $grey;
  font-size: 12px;
  line-height: 25px;
  letter-spacing: 0.24px;

  p {
    margin-top: 10px;
  }
}

.policy__link {
  text-decoration: underline;
  font-weight: bold;
  color: #3191d0;

  &:hover {
    text-decoration: none;
  }
}

.cookie-policy__btn {
  margin: 30px 50% 0;
  transform: translateX(-50%);
}