.burger-menu {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  background-color: #def9e7;
  z-index: 100000000000000000;

  .container {
    padding: 30px 15px;
  }

  .navigation {
    flex-direction: column;
    margin-left: 0;
    flex: 1 0 80%;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 100px;
      height: 120%;
      background-color: #fff;
      width: 120%;
      z-index: -1;
    }
  }

  .navigation__link {
    color: $forest-green;
    font-size: 36px;
    line-height: 42px;
    margin-top: 25px;
    transition: all 0.25s ease-out;

    &:hover {
      transform: translateX(-30px);
      color: #000;
    }
  }

  .footer-social {
    margin: 9vh 0 0 50%;
    transform: translateX(-40%);
  }
}

.burger-container {
  display: flex;
  height: 60vh;
  margin-top: 8vh;
}

.burger-options {
  background-color: $light-green;
  flex: 1 0 24.7%;
  text-align: center;
  height: 120%;
  padding-top: 100px;

  .language-btn {
    margin: 70% auto 0;
  }
}

.burger__link {
  text-decoration: underline;
  font-weight: bold;
  color: #3191d0;

  &:hover {
    text-decoration: none;
  }
}