.single-case {
  position: absolute;
  top: 5%;
  left: -10%;
  width: 90vw;
  overflow: hidden;
  height: 90%;
  max-height: 750px;
  display: none;
  padding: 70px 0 60px 80px;
  background-color: #fff;
  box-shadow: 0 0 30px 10px rgba(0, 0, 0, 0.1);

  .case__icon {
    margin:0;
    transform: translate(-20px, -20px);
    z-index: 1;
  }
}

.is-shown {
  display: block;
}



.case-info {
  margin: 90px 0 0 40px;
  text-align: left;

  @media screen and (min-width: $medium-screen) {
    width: 45%;
    padding-top: 0;
  }
}

.case-img {
  position: absolute;
  right: 0;
  top: 0;
  height: 450px;
  object-fit: cover;

  @media screen and (min-width: $medium-screen) {
    height: 100%;
    transform: translateX(60%);
  }
}

.case-info__heading {
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  margin-top: 41px;
  text-transform: uppercase;
  color: $forest-green;
}

.case-info__text {
  line-height: 33px;
  letter-spacing: 0.3px;
  margin-top: 14px;

  &--tech {
    color: $grey;
    margin-top: 70px;
  }

  strong {
    font-weight: bold;
  }
}

.case-download {
  margin-top: 30px;
  width: 170px;

}

.case-order {
  margin-top: 30px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 50px;
  }
}

.single-case__close {
  position: absolute;
  right: 40px;
  top: 40px;
  z-index: 1;
  outline: 0;
  cursor: pointer;
  transition: 0.25s ease-out;

  &:hover {
    transform: scale(1.3);
  }
}
