.navigation {
  display: flex;
  justify-content: space-between;
  padding-top: 23px;
  margin-left: 20%;
}

.navigation__link {
  font-size: 12px;
  text-transform: capitalize;
  font-weight: bold;
  line-height: 14px;
  margin-right: 27px;
  color: #000;

  &:hover {
    color: $forest-green;
  }

  &:last-child {
    margin-right: 0;
  }
}