.services-page {
  .services {
    margin-top: 80px;
    display: flex;
    padding: 0 0 110px;
    flex-wrap: wrap;
    position: relative;

    .services__item {
      flex: 1 0 20%;
      text-align: center;
      margin-top: 85px;
      cursor: pointer;

      &:hover {
        .services__item-caption {
          color: #000;
          transform: scale(1.3);
          text-align: center;
        }
      }
    }

    .services__description-article {
      text-align: center;
      letter-spacing: 0.4px;
    }

    .services__description-heading {
      font-weight: bold;
      font-size: 20px;
      line-height: 41px;
      margin-top: -30px;
    }

    .services__item-caption {
      max-width: 100px;
      margin: 26px auto 0;
      color: $grey;
      font-weight: 600;
      font-size: 12px;
      text-align: left;
      line-height: 2;
      padding-left: 10px;
      z-index: -1;
      transition: all 0.25s linear;
    }

    .services__img {
      width: 65px;
      height: 65px;
      display: block;
      margin: 0 auto;
    }
  }
}