.case-wrapper {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  padding: 70px 15px 79px;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    margin-top: 100px;
  }
}

.case {
  margin: 20px;
  flex: 1 0 auto;
  transition: all 1s ease;
  text-align: center;
  height: 360px;
  width: 50%;
  cursor: pointer;
  border: 1px #D1DBE3 solid;

  @media screen and (min-width: $tablet-screen) {
    width: 30%;
  }
}

.case-filters {
  text-align: left;
  padding-top: 25px;
  border: 0;
}

.case-filters__item {
  font-size: 18px;
  font-weight: bold;
  line-height: 30px;
  color: $grey;
  position: relative;
  padding: 3px;
  cursor: pointer;

  &:hover {
    color: $forest-green;
  }

  &.is-active {
    .case-filters__border {
      border: 1px solid $forest-green;
      color: $forest-green;
    }
  }
}

.case__icon {
  display: block;
  margin-top: 34%;
  margin-left: 50%;
  position: relative;
  z-index: -100;
  transform: translate(-50%, -50%);
}

.case-filters__border {
  padding: 3px 15px;
  border: 1px solid transparent;
  display: inline-block;
}