.contact-us {
  .intro__text {
    max-width: 340px;
  }

  .contacts__info {
    max-width: none;
  }

  .contacts__heading {
    margin: 60px 0 23px;
  }

  .contacts-block {
    text-align: left;
    margin-top: 60px;
    padding: 0;
  }

  .contacts-form {
    max-width: 400px;
    margin-top: 55px;
  }
}

.contacts-info {
  padding: 80px 7% 120px;
}

.contacts {
  flex: 1 0 auto;
}

.contacts__heading {
  color: #BDBDBD;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 14px;
  margin-bottom: 21px;
}

.contacts__info {
  font-size: 18px;
  line-height: 37px;
  color: #000;
  max-width: 250px;

  &:hover {
    color: $forest-green;
  }
}

.contacts__email {
  max-width: 353px;
  transition: all 0.3s;

  &:hover {
    transform: scale(1.05);
    text-shadow: 4px 4px 30px rgba(142, 137, 145, 0.37);
  }
}

.burger__link {
  text-decoration: underline;
  font-weight: bold;
  color: #3191d0;

  &:hover {
    text-decoration: none;
  }
}