.trends-item__img { visibility: hidden; position: absolute; width: 100%; height: 100%; top: 0; left: 0; z-index: -1; }

.trends-item__date { font-size: 14px; font-weight: bold; color: #009183; text-transform: uppercase; letter-spacing: 2px; }

.trends-item__title, .trends-item__text { margin-top: 35px; font-weight: bold; font-size: 18px; letter-spacing: 0.5px; line-height: 35px; }

.trends-item__text { margin-top: 20px; font-weight: 300; color: #808080; }

.trends-item__link { margin: 63px auto 30px; }

.more-trends { width: 150px; margin: 80px auto 0; }

.case-wrapper { margin-top: 50px; display: flex; flex-wrap: wrap; padding: 70px 15px 79px; position: relative; }

@media screen and (min-width: 991px) { .case-wrapper { margin-top: 100px; } }

.case { margin: 20px; flex: 1 0 auto; transition: all 1s ease; text-align: center; height: 360px; width: 50%; cursor: pointer; border: 1px #D1DBE3 solid; }

@media screen and (min-width: 768px) { .case { width: 30%; } }

.case-filters { text-align: left; padding-top: 25px; border: 0; }

.case-filters__item { font-size: 18px; font-weight: bold; line-height: 30px; color: #808080; position: relative; padding: 3px; cursor: pointer; }

.case-filters__item:hover { color: #009183; }

.case-filters__item.is-active .case-filters__border { border: 1px solid #009183; color: #009183; }

.case__icon { display: block; margin-top: 34%; margin-left: 50%; position: relative; z-index: -100; transform: translate(-50%, -50%); }

.case-filters__border { padding: 3px 15px; border: 1px solid transparent; display: inline-block; }

.single-case { position: absolute; top: 5%; left: -10%; width: 90vw; overflow: hidden; height: 90%; max-height: 750px; display: none; padding: 70px 0 60px 80px; background-color: #fff; box-shadow: 0 0 30px 10px rgba(0, 0, 0, 0.1); }

.single-case .case__icon { margin: 0; transform: translate(-20px, -20px); z-index: 1; }

.is-shown { display: block; }

.case-info { margin: 90px 0 0 40px; text-align: left; }

@media screen and (min-width: 991px) { .case-info { width: 45%; padding-top: 0; } }

.case-img { position: absolute; right: 0; top: 0; height: 450px; object-fit: cover; }

@media screen and (min-width: 991px) { .case-img { height: 100%; transform: translateX(60%); } }

.case-info__heading { font-size: 14px; font-weight: bold; line-height: 16px; margin-top: 41px; text-transform: uppercase; color: #009183; }

.case-info__text { line-height: 33px; letter-spacing: 0.3px; margin-top: 14px; }

.case-info__text--tech { color: #808080; margin-top: 70px; }

.case-info__text strong { font-weight: bold; }

.case-download { margin-top: 30px; width: 170px; }

.case-order { margin-top: 30px; }

@media screen and (min-width: 991px) { .case-order { margin-top: 50px; } }

.single-case__close { position: absolute; right: 40px; top: 40px; z-index: 1; outline: 0; cursor: pointer; transition: 0.25s ease-out; }

.single-case__close:hover { transform: scale(1.3); }

.services-page .service-single { position: absolute; background-color: #fff; z-index: 100000; top: 0; height: 600px; padding-top: 52px; display: none; box-shadow: 11px 15px 30px 1px rgba(68, 68, 68, 0.1); left: 50%; transform: translateX(-50%); text-align: center; width: 100%; }

.services-page .service-single .button { width: 230px; margin: 60px auto; }

.services-page .services__description-heading { font-weight: bold; font-size: 20px; line-height: 41px; margin-top: -30px; }

.services-page .services__description-text { max-width: 580px; text-align: left; line-height: 2; font-size: 18px; font-weight: 300; margin: 25px auto 0; }

.services-page .services__description-article { margin-top: 80px; }

.contact-us .intro__text { max-width: 340px; }

.contact-us .contacts__info { max-width: none; }

.contact-us .contacts__heading { margin: 60px 0 23px; }

.contact-us .contacts-block { text-align: left; margin-top: 60px; padding: 0; }

.contact-us .contacts-form { max-width: 400px; margin-top: 55px; }

.contacts-info { padding: 80px 7% 120px; }

.contacts { flex: 1 0 auto; }

.contacts__heading { color: #BDBDBD; font-weight: bold; text-transform: uppercase; font-size: 14px; margin-bottom: 21px; }

.contacts__info { font-size: 18px; line-height: 37px; color: #000; max-width: 250px; }

.contacts__info:hover { color: #009183; }

.contacts__email { max-width: 353px; transition: all 0.3s; }

.contacts__email:hover { transform: scale(1.05); text-shadow: 4px 4px 30px rgba(142, 137, 145, 0.37); }

.burger__link { text-decoration: underline; font-weight: bold; color: #3191d0; }

.burger__link:hover { text-decoration: none; }

.services-wrapper { background-color: rgba(84, 189, 130, 0.1); margin-top: 80px; padding: 83px 0 110px; }

.services-wrapper .arrow-link { bottom: -70px; }

@media screen and (min-width: 420px) { .services-wrapper .arrow-link { bottom: -300px; } }

.services-wrapper .arrow-link--prev { left: 36.5%; }

@media screen and (min-width: 768px) { .services-wrapper .arrow-link--prev { left: 42.5%; } }

@media screen and (min-width: 991px) { .services-wrapper .arrow-link--prev { left: 45.5%; } }

.services-wrapper .arrow-link--next { right: 36.5%; }

@media screen and (min-width: 768px) { .services-wrapper .arrow-link--next { right: 42.5%; } }

@media screen and (min-width: 991px) { .services-wrapper .arrow-link--next { right: 45.5%; } }

.services-wrapper .slick-slide { text-align: center; margin-top: 50px; width: 235px; outline: 0; position: relative; cursor: pointer; }

.services-wrapper .slick-slide::before { content: ''; position: absolute; background-color: #fff; width: 100%; height: 100%; border-radius: 50%; top: 0; left: 0; opacity: 0; box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2); }

.services-wrapper .slick-slide.slick-center::before { opacity: 1; transition: opacity 1.5s ease-out; }

.services-wrapper .slick-current.slick-center { margin-top: 0; border-radius: 50%; }

@media screen and (min-width: 420px) { .services-wrapper .slick-current.slick-center { width: 235px; height: 235px; } }

.services-wrapper .slick-current.slick-center .services__item-caption { display: none; }

.services-wrapper .slick-current.slick-center .services__img { border: 0; transform: scale(1.5); padding: 0; border-radius: 0; transition: all 0.5s ease; margin-top: 30%; }

.services-wrapper .slick-track { height: 230px; }

.services-wrapper .services__item-wrapper { margin-top: 57px; }

.services-wrapper .services__description-article { text-align: center; letter-spacing: 0.4px; }

.services-wrapper .services__description-heading { font-weight: bold; font-size: 20px; line-height: 41px; margin-top: -30px; }

.services-wrapper .services__description-text { color: #009183; font-size: 18px; line-height: 37px; max-width: 590px; margin: 18px auto 0; }

.services-wrapper .services__item-caption { max-width: 80%; margin: 26px auto 0; color: #808080; }

.services-wrapper .services__img { width: 65px; height: 65px; border: 1px solid #54BD82; padding: 13px; border-radius: 50%; display: block; margin: 0 auto; }

.error { padding: 30px 15px 100px; position: relative; }

.error:before { content: '404'; position: absolute; top: 50%; right: 13%; transform: translateY(-50%); font-weight: 600; font-size: 340px; color: #009183; }

.error .error-text { text-transform: capitalize; color: #000; font-size: 24px; font-weight: 300; top: 290px; }

.error .button--back { bottom: 213px; }

.error .intro__img::before { background: rgba(84, 189, 130, 0.1); }
