.footer {
  background-color: $light-green;
  padding: 35px 15px;

  @media screen and (min-width: $medium-screen) {
    padding: 80px 15px;
  }
}

.footer-top {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  .policy__link {
    margin-top: 23px;
    font-size: 12px;
  }
}


.footer-middle,
.footer-bottom {
  margin-top: 80px;
  display: flex;
  justify-content: space-between;
}

.footer-bottom-section {
  flex: 1 0 auto;
}

.copyrights {
  max-width: 145px;
  color: #808080;
  font-size: 14px;
  line-height: 25px;
}

.footer-bottom__policy {
  color: #2F80ED;
  line-height: 24px;
  font-size: 12px;
  margin-right: 11.5%;
}

.footer-social {
  display: flex;
}

.footer-social__icon {
  border: 2px $forest-green solid;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  text-align: center;
  padding: 2.5px;
  margin-left: 10px;

  &:hover {
    background-color: $forest-green;

    path {
      fill: #fff;
    }
  }
}