.header-wrapper {
  width: 100%;
  padding: 25px 15px;
  z-index: 3;

  button {
    outline: 0;
    cursor: pointer;
    transition: transform 0.25s;

    &:hover {
      transform: scale(1.3);
    }
  }

  @media screen and (min-width: $medium-screen) {
    position: fixed;
  }
}

.header {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;


  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  .policy__link {
    margin-top: 23px;
    font-size: 12px;
  }
}

.language {
  position: relative;


  img {
    margin-left: 5px;
  }
}

.language-btn {
  width: 70px;
}

.is-hidden {
  position: absolute;
  top: 60px;
  display: none;
  left: 15px;
}

.is-rotated {
  transform: rotate(180deg);
  transition: all 0.2s ease;
}

.language__option {
  color: $forest-green;
  cursor: pointer;
}

.header-wrapper--main {
  background-color: rgba(255, 255, 255, 0.83);

  > .container {
    max-width: 1200px!important;
  }
}

.header-wrapper > .container {
  max-width: 90vw;
}