.arrow-link--prev {
  position: absolute;
  z-index: 1;
  left: 34.5%;

  @media screen and (min-width: $tablet-screen){
    left: 42.5%;
  }

  @media screen and (min-width: $medium-screen) {
    left: 45%;
  }
}

.arrow-link--next {
  position: absolute;
  right: 34.5%;
  padding: 0!important;
  z-index: 1;

  img {
    transform: rotate(180deg);
  }

  @media screen and (min-width: $tablet-screen) {
    right: 42.5%;
  }

  @media screen and (min-width: $medium-screen) {
    right: 45%;
  }
}

.arrow-link {
  background-color: #fff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  bottom: -50px;
  box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2);
  transition: all 0.2s ease-out;
  padding: 12px;
  cursor: pointer;

  &:hover {
    box-shadow: none;
    background-color: $forest-green;

    path {
      fill: #ffffff;
    }
  }

  img {
    position: absolute;
    top: 30%;
    left: 40%;
  }
}