.order-wrapper {
  background-color: $light-green;
  padding: 85px 15px 70px;
}

.order-option-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  @media screen and (min-width: $tablet-screen) {
    justify-content: space-between;
    flex-wrap: nowrap;
  }
}

.order-option {
  color: $forest-green;
  padding: 30px;
  position: relative;
  height: 530px;
  transition: all 0.15s ease-out;
  min-width: 300px;
  margin-top: 50px;
  border: #54BD82 1px solid;

  &:hover {
    background-color: #fff;
    cursor: pointer;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);
    border: 0;

    .button {
      background-color: $forest-green;
      color: #fff;

      &:hover {
        box-shadow: 0 20px 40px rgba(0, 145, 131, 0.2);
      }
    }
  }

  @media screen and (min-width: $tablet-screen) {
    flex-basis: 46%;
    min-width: initial;
    height: 500px;
    margin-top: 106px;
  }

  @media screen and (min-width: $laptop-screen) {
    padding: 66px;
  }
}

.order-option__price {
  line-height: 61px;
  font-size: 35px;
  font-weight: bold;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    font-size: 48px;
  }

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: $forest-green;
    bottom: -29px;
  }
}

.order-option__description {
  margin-top: 73px;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 2px;
}

.order-option__description-text {
  margin-top: 43px;
  color: $grey;
  font-size: 18px;
  line-height: 37px;
  max-width: 372px;
}

.order-option__link {
  position: absolute;
  bottom: 30px;
  left: 30px;

  @media screen and (min-width: $laptop-screen) {
    bottom: 67px;
    left: 60px;
  }
}