.error {
  padding: 30px 15px 100px;
  position: relative;

  &:before {
    content: '404';
    position: absolute;
    top: 50%;
    right: 13%;
    transform: translateY(-50%);
    font-weight: 600;
    font-size: 340px;
    color: #009183;
  }

  .error-text {
    text-transform: capitalize;
    color: #000;
    font-size: 24px;
    font-weight: 300;
    top: 290px;
  }

  .button--back {
    bottom: 213px;
  }

  .intro__img {
    &::before {
      background: $light-green;
    }
  }
}