.button {
  width: 130px;
  border: $forest-green 2px solid;
  text-align: center;
  padding: 11px 0;
  color: $forest-green;
  border-radius: 25px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: block;
  letter-spacing: 0.02px;
  outline: 0;
  font-weight: bold;
  text-transform: capitalize;
  font-family: 'Gotham Pro';

  &:hover {
    box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2);
  }
}

.button--green {
  background-color: $forest-green;
  color: #fff;

  &:hover {
    box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2);
  }
}

.button--intro {
  height: 60px;
  margin-top: 20px;
  position: relative;
  z-index: 2;
  bottom: 140px;
  left: 20px;
  width: 200px;
  border-radius: 30px;
  padding-top: 20px;
  font-size: 18px;
}

.button--back {
  background-color: #fff;
  bottom: 250px;
}