.section-heading {
  font-size: 48px;
  font-weight: bold;
  text-align: center;
  line-height: 61px;
  text-transform: capitalize;
}

.section-caption {
  font-size: 12px;
  line-height: 2;
  text-align: center;
  color: $grey;
  letter-spacing: 0.24px;
  margin-top: 25px;

  @media screen and (min-width: $tablet-screen) {
    margin-top: 50px;
  }
}

.light-text {
  font-size: 48px;
  font-weight: 300;
  line-height: 61px;
  display: block;
  color: $forest-green;
}