.contacts-block {
  padding: 100px 15px 50px;
  text-align: center;
  position: relative;

  @media screen and (min-width: $medium-screen) {
    padding: 105px 15px 230px;
  }
}

.contacts__item {
  color: $grey;
  font-size: 12px;
  display: block;
  line-height: 2;
  max-width: 310px;
  margin: 20px auto 0;

  &:first-child {
    margin-top: 70px;
  }

  &:hover {
    text-decoration: underline;
  }

  &:last-child:hover {
    text-decoration: none;
  }
}

.contacts-form {
  flex: 1 0 30%;
  margin-top: 80px;
}

.contacts-wrapper {
  display: flex;
  justify-content: space-between;
}

.contacts__slogan {
  text-align: left;
  padding-left: 10%;
  flex: 1 0 50%;

  .light-text {
    max-width: 505px;
    margin-top: 24%;
  }
}

.contacts__field-label {
  color: $forest-green;
  font-size: 14px;
  margin: 25px 0 6px;
  display: block;
  text-align: left;
  font-weight: bold;
  text-transform: uppercase;

  &:first-child {
    margin-top: 0;
  }
}

.contacts-form__field {
  border: 1px solid rgba(128, 128, 128, 0.71);
  border-radius: 5px;
  background: #fff;
  width: 100%;
  height: 40px;
  padding: 10px;
}

.contacts-form__field--message {
  height: 120px;
  margin-top: 10px;
}

.contacts-form__submit {
  margin: 40px 0 0;

  &:hover {
    background-color: $forest-green;
    color: #fff;
  }
}