.services-wrapper {
  background-color: $light-green;
  margin-top: 80px;
  padding: 83px 0 110px;

  .arrow-link {
    bottom: -70px;

    @media screen and (min-width: $mobile-screen) {
      bottom: -300px;
    }
  }

  .arrow-link--prev {
    left: 36.5%;

    @media screen and (min-width: $tablet-screen) {
      left: 42.5%;
    }

    @media screen and (min-width: 991px) {
      left: 45.5%;
    }
  }

  .arrow-link--next {
    right: 36.5%;

    @media screen and (min-width: $tablet-screen) {
      right: 42.5%;
    }

    @media screen and (min-width: 991px) {
      right: 45.5%;
    }
  }

  .slick-slide {
    text-align: center;
    margin-top: 50px;
    width: 235px;
    outline: 0;
    position: relative;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      background-color: #fff;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      top: 0;
      left: 0;
      opacity: 0;
      box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2);
    }

    &.slick-center::before {
      opacity: 1;
      transition: opacity 1.5s ease-out;
    }
  }

  .slick-current.slick-center {
    margin-top: 0;
    border-radius: 50%;

    @media screen and (min-width: $mobile-screen) {
      width: 235px;
      height: 235px;
    }

    .services__item-caption {
      display: none;
    }

    .services__img {
      border: 0;
      transform: scale(1.5);
      padding: 0;
      border-radius: 0;
      transition: all 0.5s ease;
      margin-top: 30%;
    }
  }

  .slick-track {
    height: 230px;
  }

  .services__item-wrapper {
    margin-top: 57px;
  }

  .services__description-article {
    text-align: center;
    letter-spacing: 0.4px;
  }

  .services__description-heading {
    font-weight: bold;
    font-size: 20px;
    line-height: 41px;
    margin-top: -30px;
  }

  .services__description-text {
    color: $forest-green;
    font-size: 18px;
    line-height: 37px;
    max-width: 590px;
    margin: 18px auto 0;
  }

  .services__item-caption {
    max-width: 80%;
    margin: 26px auto 0;
    color: $grey;
  }

  .services__img {
    width: 65px;
    height: 65px;
    border: 1px solid #54BD82;
    padding: 13px;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
  }
}