.intro {
  padding: 20px 0 0;

  .arrow-link {
    bottom: -55px;

    @media screen and (min-width: $tablet-screen) {
      bottom: -20px;
    }
  }

  .slick-list {
    min-height: 450px;
  }
}

.intro__wrapper {
  position: relative;
}

.intro__img {
  display: block;
  max-width: none;
  position: relative;
  z-index: -3;
  text-align: center;
  max-height: 470px;
  margin: 0 auto;
  height: 100%;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
    left: 0;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.73), rgba(0, 0, 0, 0));
  }

  @media screen and (min-width: $tablet-screen) {
    max-width: 80%;
    min-height: 500px;
  }

  img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}

.intro__text {
  position: relative;
  left: 20px;
  top: 358px;
  font-size: 30px;
  line-height: 61px;
  font-weight: bold;
  max-width: 500px;
  color: $forest-green;

  @media screen and (min-width: $tablet-screen){
    font-size: 48px;
  }
}

.intro__header {
  display: block;
  font-size: 48px;
  color: #000;
  max-width: 375px;
  text-transform: capitalize;
  top: 280px;
  line-height: 86px;

  @media screen and (min-width: $tablet-screen){
    font-size: 64px;
  }
}


.slide__text-header {
  display: block;
  font-size: 48px;
  color: #000;

  @media screen and (min-width: $tablet-screen){
    font-size: 64px;
  }
}


