.services-page {
  .service-single {
    position: absolute;
    background-color: #fff;
    z-index: 100000;
    top: 0;
    height: 600px;
    padding-top: 52px;
    display: none;
    box-shadow: 11px 15px 30px 1px rgba(68, 68, 68, 0.1);
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    width: 100%;

    .button {
      width: 230px;
      margin: 60px auto;
    }
  }

  .services__description-heading {
    font-weight: bold;
    font-size: 20px;
    line-height: 41px;
    margin-top: -30px;
  }

  .services__description-text {
    max-width: 580px;
    text-align: left;
    line-height: 2;
    font-size: 18px;
    font-weight: 300;
    margin: 25px auto 0;
  }

  .services__description-article {
    margin-top: 80px;
  }
}