.trends {
  padding: 100px 15px;

}

.trends-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 105px;
}

.trends-item {
  flex: 1 0 30%;
  margin-right: 35px;
  border: 1px solid #D1DBE3;
  padding: 30px 30px 10px;
  position: relative;
  object-fit: cover;
  transition: all 0.25s;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    transform: translateY(-20px);

    .trends-item__img {
      visibility: visible;
    }

    .trends-item__title,
    .trends-item__text {
      color: #fff;
    }

    .trends-item__link {
      background-color: $forest-green;
      color: #fff;
    }
  }
}

.trends-item__img {
  visibility: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.trends-item__date {
  font-size: 14px;
  font-weight: bold;
  color: $forest-green;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.trends-item__title,
.trends-item__text {
  margin-top: 35px;
  font-weight: bold;
  font-size: 18px;
  letter-spacing: 0.5px;
  line-height: 35px;
}

.trends-item__text {
  margin-top: 20px;
  font-weight: 300;
  color: #808080;
}

.trends-item__link {
  margin: 63px auto 30px;
}

.more-trends {
  width: 150px;
  margin: 80px auto 0;
}